import API_ENDPOINT from '../../globals/api-endpoint';
import CONFIG from '../../globals/config';

const createRestaurantItemTemplate = (restaurant) => `
  <div class="restaurant-item">
    <div class="restaurant-item__header">
      <img class="restaurant-item__header__poster" alt="${restaurant.name}"
           src="${API_ENDPOINT.IMAGE(restaurant.pictureId, 'small')}">
      <div class="restaurant-item__header__rating">
        <p><span class="restaurant-item__header__rating__score">${restaurant.rating}</span></p>
      </div>
    </div>
    <div class="restaurant-item__content">
      <h3><a href="/#/detail/${restaurant.id}">${restaurant.name}</a></h3>
      <p>${restaurant.description}</p>
    </div>
  </div>
`;

const createRestaurantDetailTemplate = (restaurant) => `
  <div class="restaurant__container">
    <img class="restaurant__poster" src="${CONFIG.BASE_URL}images/medium/${restaurant.pictureId}" alt="${restaurant.name}" />
    <div class="restaurant__info">
      <h2 class="restaurant__name">${restaurant.name}</h2>
      <h3>Information</h3>
      <h4>City</h4>
      <p>${restaurant.city}</p>
      <h4>Address</h4>
      <p>${restaurant.address}</p>
      <h4>Rating</h4>
      <p>${restaurant.rating}</p>
    </div>
  </div>
  <div class="restaurant__description" style="border: 1px solid #ccc; padding: 10px; margin-bottom: 20px;">
    <h3>Description</h3>
    <p>${restaurant.description}</p>
  </div>
  <div class="restaurant__menu" style="display: flex; margin-bottom: 20px;">
    <div class="menu__column" style="border: 1px solid #ccc; padding: 10px; flex: 1;">
      <h3>Food</h3>
      <ul>
        ${restaurant.menus.foods.map((food) => `<li>${food.name}</li>`).join('')}
      </ul>
    </div>
    <div class="menu__column" style="border: 1px solid #ccc; padding: 10px; flex: 1; margin-left: 20px;">
      <h3>Drinks</h3>
      <ul>
        ${restaurant.menus.drinks.map((drink) => `<li>${drink.name}</li>`).join('')}
      </ul>
    </div>
  </div>
  <div class="restaurant__reviews"></div> <!-- Container for reviews -->
`;




export function createRestaurantReviewsTemplate(reviews) {
  let reviewsHtml = '';
  reviews.forEach((review) => {
    reviewsHtml += `
      <div class="review">
        <p>${review.name}</p>
        <p>${review.review}</p>
      </div>
    `;
  });
  return reviewsHtml;
}

const createRestoLikeButtonTemplate = () => `
  <button aria-label="like this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`;

const createRestoLikedButtonTemplate = () => `
  <button aria-label="unlike this restaurant" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`;

export {
  createRestaurantItemTemplate, createRestaurantDetailTemplate,
  createRestoLikeButtonTemplate, createRestoLikedButtonTemplate,
};
