const API_ENDPOINT = {
  LIST: 'https://restaurant-api.dicoding.dev/list',
  DETAIL: (id) => `https://restaurant-api.dicoding.dev/detail/${id}`,
  SEARCH: (query) => `https://restaurant-api.dicoding.dev/search?q=${query}`,
  REVIEW: 'https://restaurant-api.dicoding.dev/review',
  IMAGE: (pictureId, size) => `https://restaurant-api.dicoding.dev/images/${size}/${pictureId}`,
};

export default API_ENDPOINT;
