// detail.js

import UrlParser from '../../routes/url-parser';
import RestaurantSource from '../../data/restaurantdb-source';
import { createRestaurantDetailTemplate, createRestaurantReviewsTemplate } from '../templates/template-creator';
import LikeButtonInitiator from '../../utils/like-button-initiator';

const Detail = {
  async render() {
    return `
            <div id="restaurant" class="restaurant"></div>
            <div id="likeButtonContainer"></div>
            <div id="addReviewForm" class="add-review-form">
                <h3>Add a Review</h3>
                <form id="reviewForm">
                    <div class="form-group">
                        <label for="reviewerName">Your Name</label>
                        <input type="text" id="reviewerName" name="reviewerName" required>
                    </div>
                    <div class="form-group">
                        <label for="reviewContent">Your Review</label>
                        <textarea id="reviewContent" name="reviewContent" required></textarea>
                    </div>
                    <button type="submit">Submit Review</button>
                </form>
            </div>
        `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurant = await RestaurantSource.detailRestaurant(url.id);
    const restaurantsContainer = document.querySelector('#restaurant');
    restaurantsContainer.innerHTML = createRestaurantDetailTemplate(restaurant);

    const reviewTemplate = createRestaurantReviewsTemplate(restaurant.customerReviews);
    restaurantsContainer.insertAdjacentHTML('beforeend', reviewTemplate);

    LikeButtonInitiator.init({
      likeButtonContainer: document.querySelector('#likeButtonContainer'),
      restaurant: {
        id: restaurant.id,
        city: restaurant.city,
        name: restaurant.name,
        rating: restaurant.rating,
        pictureId: restaurant.pictureId,
      },
    });

    const form = document.getElementById('reviewForm');
    form.addEventListener('submit', async (event) => {
      event.preventDefault(); // Prevent default form submission

      // Get reviewer name and review content from the form
      const reviewerName = document.getElementById('reviewerName').value;
      const reviewContent = document.getElementById('reviewContent').value;

      // Save the review to the server
      const newReview = {
        name: reviewerName,
        review: reviewContent,
      };
      // eslint-disable-next-line max-len, no-use-before-define
      await saveReviewToServer(newReview); // Implement this function based on how you save reviews to the server

      // Display the newly added review on the page
      const reviewContainer = document.querySelector('.restaurant__reviews');
      const newReviewElement = document.createElement('div');
      newReviewElement.classList.add('review');
      newReviewElement.innerHTML = `
                <p>${reviewerName}</p>
                <p>${reviewContent}</p>
            `;
      reviewContainer.appendChild(newReviewElement);

      // Clear the form after adding the review
      form.reset();
    });
  },
};

async function saveReviewToServer(reviewData) {
  try {
    const response = await fetch('https://restaurant-api.dicoding.dev/review', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(reviewData),
    });

    if (!response.ok) {
      throw new Error('Failed to save review to server');
    }

    const responseData = await response.json();
    console.log('Review saved:', responseData);
  } catch (error) {
    console.error('Error saving review:', error);
  }
}

export default Detail;
